<template>
  <div class="cont-item-fa" @click="toPilotDetail(id)">
    <div class="cont-item">
      <div class="cont-img"></div>
      <div class="cont-info">
        <div class="cont-number">
         {{contNumber}}
        </div>
        <div class="cont-detail">
          {{contDetail}}
        </div>
      </div>
      <div class="cont-ope">
        <Icon type="ios-arrow-right" class="cont-ope-icon"></Icon>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    props: {
      contNumber: {
        type: String,
        required: true
      },
      contDetail: {
        type: String,
        required: true
      },
      id: {
        type: String,
        required: true
      },
    },
    methods:{
      toPilotDetail(id){
        console.log(id)
        this.$router.push({
          path: '/medicalHistory',
          params: {
            id:id
          }
        })
      },
    }
  }
</script>

<style scoped>
  .cont-item{
    background: #fff;
    padding:20px;
    height:80px;
    border-bottom: 1px solid #eee;
    cursor: pointer;
  }
  .cont-item:hover{
    background: #f9f9f9;
  }
  .cont-item-fa:first-child
  {
    border-top: 1px solid #eee;
  }
  .cont-img{
    width:40px;
    height:40px;
    border-radius:50%;
    background: #eee;
    display: inline-block;
    margin:0 20px 0 50px;
  }
  .cont-info{
    display: inline-block;
    height:40px;
    vertical-align: 5px;
  }
  .cont-number{
    color:#495060;
    font-size: 16px;
    line-height: 20px;
    height:20px;
  }
  .cont-detail{
    color:#9da2ad;
    font-size: 14px;
    line-height: 20px;
    height:20px;
  }
  .cont-ope{
    float:right;
    margin:11px 50px 0 0;
    display: inline-block;
  }
  .cont-ope-icon{
    font-size:20px;
    color:#9da2ad;
    opacity: 0.7;
  }
</style>



