import Vue from 'vue'
import Router from 'vue-router'

const Login = () => import('../pages/login/login')
const Layout = () => import('../pages/layout/layout')
const Home = () => import('../pages/home/home')
const ProjectManage = () => import('../pages/projectManage/projectManage')
const LaboratoryManage = () => import('../pages/laboratoryManage/laboratoryManage')
const UserManage = () => import('../pages/userManage/userManage')
const SettingManage = () => import('../pages/settingManage/settingManage')
const ReportManage = () => import('../pages/reportManage/reportManage')
const ReportRightsManage = () => import('../pages/reportRightsManage/reportRightsManage')
const MedicalHistory = () => import('../pages/home/MedicalHistory')
Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/edc',
      name: 'edc',
      component: Layout,
      children: [
        {
          path: '/home',
          name: 'home',
          component: Home
        },
        {path: '/projectManage', name: 'projectManage', component: ProjectManage},
        {path: '/laboratoryManage', name: 'laboratoryManage', component: LaboratoryManage},
        {path: '/userManage', name: 'userManage', component: UserManage},
        {path: '/settingManage', name: 'settingManage', component: SettingManage},
        {path: '/reportManage', name: 'reportManage', component: ReportManage},
        {path: '/reportRightsManage', name: 'reportRightsManage', component: ReportRightsManage},
        {path: '/medicalHistory', name: 'medicalHistory', component: MedicalHistory}

      ]
    }
  ]
})

export default router



